.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.card {
  max-width: 130px;
  max-height: 130px;
  margin: 1rem;
  position: relative;
  transition: all .2s ease-in-out;
}

.card-img-top {
  height: 100%;
  overflow: hidden;
  background:rgb(255, 255, 255);
}

.card:hover {
  transform: scale(1.1);
  cursor: pointer;
}

.card:active {
  transform: translate(1px, 1px);
}

#card-container {
  margin-top: 10px;
}
